* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.moduleContainer {
  max-width: 1440px;
  margin: auto;
  padding: 20px;
}
.moduleContainer img {
  width: 100%;
  height: auto;
}
.moduleContainer .moduleContent .title {
  font-size: 42px;
  font-weight: 700;
}
.moduleContainer .moduleContent .desc {
  margin-top: 12px;
  margin-bottom: 16px;
}

@media screen and (min-width: 768px) {
  .moduleContainer {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .moduleContainer .moduleContent {
    width: 380px;
  }
  .moduleContainer .moduleContent .desc {
    margin-top: 24px;
    margin-bottom: 32px;
  }
  .moduleContainer img {
    width: 42%;
    max-width: 505px;
    margin-right: 7%;
  }
}